import React from "react";
import emailjs from "@emailjs/browser";
import { useState } from "react";
import Loader from "./Loader";


const Contact = ({content}) => {
  const [isLoading, setIsloading] = useState(false);
  const sendEmail = (e) => {
    setIsloading(true);
    e.preventDefault();
    const name = e.target.name.value;
    console.log(name);
    emailjs
      .sendForm(
        "service_lzipgfk",
        "template_uwnhwvp",
        e.target,
        "2MZukBcCtV4u3pM_E"
      )
      .then((res) => {
        if (res.status === 200) {
          setIsloading(false);
          e.target.reset();
        }
      });
  };
  return (
    <div className="">
      <div className="" style={{ backgroundColor: "#2d1950" }}>
        <div className="container mx-auto py-4 row fw-bold justify-content-center align-items-center">
          <div className="text-center col-12 col-md-6 lg:text-left text-white">
            <h1 className="text-5xl font-bold">{content?.email_heading}</h1>
            <p className="py-6">
            {content?.email_slogan}
            </p>
            <button className="d-block d-md-none btn mb-4 btn-primary mx-auto">
              <a
                className="text-decoration-none text-light"
                href="mailto: nmahmudcu@gmail.com"
              >
                Send Email
              </a>
            </button>
          </div>
          <div className="card col-12 col-md-6">
            <div className="card-body text-start position-relative">
              {isLoading && <Loader />}
              <form onSubmit={sendEmail}>
                <div className="mb-3">
                  <label htmlFor="exampleInputEmail1" className="form-label text-start">
                    Your Name
                  </label>
                  <input type="text" className="form-control" name="name" />
                </div>
                <div className="mb-3">
                  <label htmlFor="exampleInputEmail1" className="form-label">
                    Email address
                  </label>
                  <input
                    type="email"
                    className="form-control"
                    id="exampleInputEmail1"
                    name="email"
                    aria-describedby="emailHelp"
                  />
                  <div id="emailHelp" className="form-text">
                    We'll never share your email with anyone else.
                  </div>
                </div>
                <div className="mb-3">
                  <label className="form-label">Your Messages</label>
                  <div className="form-floating">
                    <textarea
                      className="form-control"
                      placeholder="Leave a comment here"
                      name="message"
                      id="floatingTextarea2"
                      style={{ height: "100px" }}
                    ></textarea>
                    <label htmlFor="floatingTextarea2">Comments</label>
                  </div>
                </div>

                <button type="submit" value={"SEND"} className="btn btn-primary">
                  Submit
                </button>
              </form>
            
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Contact;
