function ResumeNew() {
  const [width, setWidth] = useState(1200);
  const [data, setData] = React.useState({});
  React.useEffect(() => {
    fetch(
      "https://raw.githubusercontent.com/nmahmudcu/Portfolio-Data/main/data.json"
    )
      .then((res) => res.json())
      .then((result) => console.log(result));
  }, []);

  useEffect(() => {
    setWidth(window.innerWidth);
  }, []);

  return (
    <div>
      <Container fluid className={data.resume.hidden ? "d-none" :"resume-section"}>
        <Row style={{ justifyContent: "center", position: "relative" }}>
          <Button
            variant="primary"
            href={data?.resume.download_link}
            target="_blank"
            style={{ maxWidth: "250px" }}
          >
            <AiOutlineDownload />
            &nbsp;Download CV
          </Button>
        </Row>

        <Row className="resume">
          <Document file={data?.resume?.git_link} className="d-flex justify-content-center">
            <Page pageNumber={1} scale={width > 786 ? 1.7 : 0.6} />
          </Document>
        </Row>

        <Row style={{ justifyContent: "center", position: "relative" }}>
          <Button
            variant="primary"
            href={data?.resume?.download_link}
            target="_blank"
            style={{ maxWidth: "250px" }}
          >
            <AiOutlineDownload />
            &nbsp;Download CV
          </Button>
        </Row>
      </Container>
    </div>
  );
}

export default ResumeNew;
