import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import homeLogo from "../../Assets/Projects/Invoice-amico.svg";
import Projects from "../Projects/Projects";
// import Particle from "../Particle";
import Home2 from "./Home2";
import Type from "./Type";
import Contact from "../Contact";

function Home() {
  const [data, setData] = React.useState({});
  React.useEffect(() => {
    fetch(
      "https://raw.githubusercontent.com/nmahmudcu/Portfolio-Data/main/data.json"
    )
      .then((res) => res.json())
      .then((result) => setData(result));
  }, []);
  return (
    <section id="home">
      <Container fluid className="home-section">
        <Container className="home-content">
          <Row>
            <Col md={7} className="home-header">
              <h1 style={{ paddingBottom: 15 }} className="heading">
                Hi There!{" "}
                <span className="wave" role="img" aria-labelledby="wave">
                  
                </span>
              </h1>

              <h1
                className="heading-name"
                dangerouslySetInnerHTML={{ __html: data?.section_1?.name }}
              ></h1>

              <div style={{ padding: 50, textAlign: "left" }}>
                {data.section_1 && <Type text={data?.section_1?.short_intro} />}
              </div>
            </Col>

            <Col md={5} style={{ paddingBottom: 20 }}>
              <img
                src={homeLogo}
                alt="home pic"
                className="img-fluid"
                style={{ maxHeight: "450px" }}
              />
            </Col>
          </Row>
        </Container>
      </Container>
      {data.section_2 && <Home2 content={data?.section_2} />}

      {data.section_3 && <Projects content={data?.section_3} />}
      {data.section_4 && <Contact content={data?.section_4} />}
    </section>
  );
}

export default Home;
