import React from "react";
import { Container, Row, Col, Button } from "react-bootstrap";
import myImg from "../../Assets/avatar.svg";
import Tilt from "react-parallax-tilt";
import { Link } from "react-router-dom";

function Home2({ content }) {
  const [data, setData] = React.useState({});
  React.useEffect(() => {
    fetch(
      "https://raw.githubusercontent.com/nmahmudcu/Portfolio-Data/main/data.json"
    )
      .then((res) => res.json())
      .then((result) => setData(result));
  }, []);
  return (
    <div id="about">
      <Container
        fluid
        className="home-about-section"
        style={{ backgroundColor: "#2d1950" }}
      >
        <Container>
          <Row>
            <Col md={8} className="home-about-description">
              <h1
                style={{ fontSize: "2.6em" }}
                dangerouslySetInnerHTML={{ __html: content?.heading }}
              ></h1>
              <div className="home-about-body">
                {content?.description?.map((e, index) => (
                  <p key={index} dangerouslySetInnerHTML={{ __html: e?.d }}></p>
                ))}
              </div>
            </Col>
            <Col md={4} className="myAvtar">
              <Tilt>
                <img src={myImg} className="img-fluid" alt="avatar" />
              </Tilt>
            </Col>
          </Row>
          <Row style={{ justifyContent: "center", position: "relative" }}>
            <Link className={data?.resume?.hidden && "d-none"} to="/resume">
              <Button
                variant="primary"
                href="/resume"
                target="_blank"
                style={{ maxWidth: "250px" }}
              >
                {content?.resume_title}
              </Button>
            </Link>
          </Row>
          <Row>
            <Col md={12} className="home-about-social">
              <h1>{content?.resume_heading}</h1>
              <p
                dangerouslySetInnerHTML={{ __html: content?.resume_slogan }}
              ></p>
              <ul className="home-about-social-links">
                {content.social_links && content?.social_links?.map((e, index) => (
                  <li
                    key={index}
                    className="social-icons"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={e?.platform}
                  >
                    <a
                      href={e.link}
                      target="_blank"
                      rel="noreferrer"
                      className="icon-colour  home-social-icons"
                    >
                      <span
                        dangerouslySetInnerHTML={{ __html: e?.icon }}
                        className="fs-5"
                      ></span>
                    </a>
                  </li>
                ))}
              </ul>
            </Col>
          </Row>
        </Container>
      </Container>
    </div>
  );
}
export default Home2;
